* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
  text-align: center;
}

#search-form {
  width: 330px;
margin-right: auto;
margin-left: auto;
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
  border-top: solid 5px blue;
  border-bottom: solid 5px yellow;
}

.load-more{
  /* padding: 10px 20px; */
  width: 150px;
  height: 50px;
  border-radius: 15px;
  background-color: aquamarine;
margin-left: auto;
  cursor: pointer;
  transition: all 300ms linear;
}
.load-more:hover,
.load-more:focus{
  font-size: 20px;
}
input {
   padding: 10px 20px;
}
button[type="submit"]{
 padding: 10px 20px;
 border-radius: 10px;
 cursor: pointer;
 transition: all 300ms linear;
}
button[type="submit"]:hover,
button[type="submit"]:focus{
  background-color: aquamarine;
}